<script>

const path = process.env.VUE_APP_ROOT_API

import Slider from '@vueform/slider'
import axios from 'axios'
import router from '../router'
import * as dataService from '../services/dataService';
import { FwbSpinner } from 'flowbite-vue'
import { useVuelidate } from '@vuelidate/core'
import { required, minLength, maxLength, alpha } from '@vuelidate/validators'


export default {

  setup: () => ({ v$: useVuelidate() }),
  components: {
    Slider,
    FwbSpinner
  },
  data: () => ({
    dateSlider: {
      value: [1600, 1700],
      min: 1550,
      max: 1815
    },
    // _item vars match validations
    subject_search_item: '',
    event_search_item: '',
    person_search_item: '',
    producer_search_item: '',
    producer_name: '',
    technique: '',
    search_subject_data: [],
    search_subject_index_data: [],
    search_event_data: [],
    search_person_data: [],
    search_producer_data: [],
    search_techniques: [],
    all_images: [],
    prodList: { 
        'Barlow': 'barlow', 
        'Gaywood': 'gaywood', 
        'Hollar': 'hollar', 
        'Kneller': 'kneller',
        'Lely': 'lely'
    },
    techniqueList: {
        'Drypoint': 'drypoint',
        'Engraving': 'engraving',
        'Etching': 'etching',
        'Hand-coloured': 'hand-coloured',
        'Letterpress': 'letterpress', 
        'Mezzotint': 'mezzotint',
        'Rubbing': 'rubbing',
        'Woodcut': 'woodcut'
    },
    inProdList: false,
    showDateFilter: false,
    spinnerShow: false,
    resultShow: false,
    producerDateCheckbox: null,
    eventDateCheckbox: null,
    producerListError : null
  }), 
  validations () {
    // matches v-model variables
    return {
      subject_search_item: { required, minlengthValue: minLength(3), maxLengthValue: maxLength(50), alpha }, 
      event_search_item: { required, minLengthValue: minLength(3), maxLengthValue: maxLength(50), alpha },
      person_search_item: { required, minLengthValue: minLength(3), maxLengthValue: maxLength(50), alpha },
      producer_search_item: { required, minLengthValue: minLength(3), maxLengthValue: maxLength(50), alpha }
    }
  },
  methods: {
    viewResultsPage() {
      router.push('/resultsView')
    },
    viewProducerPage() {
      router.push({
        name: 'Producer',
        params: {
          producerName: this.producer_name
        }
      })
    },
    setShow() {
      setTimeout(() => {
        this.resultShow = true;
        this.spinnerShow = false;
      }, 3000);
    },
    clearSearches() {
      localStorage.clear();
      this.subject_search_item = '';
      this.event_search_item = '';
      this.person_search_item = '';
      this.producer_search_item = '';
      this.search_subject_data = [];
      this.search_subject_index_data = [];
      this.search_event_data = [];
      this.search_person_data = [];
      this.search_producer_data = [];
      this.search_techniques = [];
      this.resultShow = false;
      this.showDateFilter = false;
      this.producerDateCheckbox = null;
      this.eventDateCheckbox = null;
      this.producerListError = null;
      this.v$.$reset();
    },

    // TODO reduce functions 
    async handleSubjectSearchRequests(e) {
      e.preventDefault();
      const result = await this.v$.subject_search_item.$validate();
      if (!result) {
        // show error message
        return;  
      }
      // reset between search requests
      this.search_subject_data = [];
      this.search_subject_index_data = [];
      this.removeMessage();
      this.resultShow = false;
      // start the spinner
      this.spinnerShow = true;
      this.getImagesBySubject(this.subject_search_item);
      this.getImagesBySubjectIndex(this.subject_search_item);
      // add a timeout so that data has a chance to load into localstorage
      this.setShow()
    },
    async handleEventSearchRequests(e) {
      e.preventDefault();
      const result = await this.v$.event_search_item.$validate();
      if (!result) {
        // show error message
        return;
      }
      // reset between search requests
      this.search_event_data = [];
      this.removeMessage();
      this.resultShow = false;
      this.spinnerShow = true;
      if (this.showDateFilter === true) {
        this.getImagesByEventDate(this.event_search_item, this.dateSlider.value);
      }
      else {
        this.getImagesByEvent(this.event_search_item);
      }
      this.setShow()
    },
    async handlePersonSearchRequests(e) {
      e.preventDefault();

      const result = await this.v$.person_search_item.$validate();
      if (!result) {
        // show error message
        return;
      }
      // reset between search requests
      this.search_person_data = [];
      this.removeMessage();
      this.resultShow = false;
      this.spinnerShow = true;
      this.getImagesByPerson(this.person_search_item);
      this.getImagesByLabelSearch(this.person_search_item);
      this.setShow()
    },
    async handleProducerSearchRequests(e) {
      e.preventDefault();

      const result = await this.v$.producer_search_item.$validate()
      if (!result) {
       // show error message
        return;
      }
      // reset between search requests
      this.search_producer_data = [];
      this.resultShow = false;
      if (this.checkInProdList(this.producer_search_item.toLowerCase())) {
        this.inProdList = true;
      }
      else {
        this.spinnerShow = true;
        this.removeMessage();
        if (this.showDateFilter === true) {
          this.getImagesByProducerDate(this.producer_search_item, this.dateSlider.value);
        }
        else {
          this.getImagesByProducer(this.producer_search_item);
        }
        this.setShow();
      }
    },
