    checkInProdList(item) {
      // search in the prodList
      if (Object.values(this.prodList).includes(item)) {
        this.producerListError = "Please use the 'Popular Producer/Printmaker' search.";
        return true;
      }
      this.producerListError = null;
      return false;
      
    },
    checkboxToggle() {
      if (this.showDateFilter == false) {
