      // use axios to call a stored procedure in the API functions, passing the search_item as 'item'
      axios
        .get(path + "image_subject_search/" + subject_search_item)
        .then((response) => {

          if (response.data.recordset === undefined || response.data.recordset.length === 0) {
            console.log('No subject data');
          }
          else {
            // get all the BPI_catKeys numbers of returned data
            this.search_subject_data = response.data.recordset;
            this.search_subject_data.forEach(function (object) {
              subject_data_bpis.push(object.BPI_catKey);
            });
          }
          if (subject_data_bpis.length !== 0) {
            this.callDataService(subject_data_bpis);
