          }
        })
        .catch(function (err) {
          console.log(err);
        })


    },
    removeMessage() {
      this.inProdList = false;
    },
    removeDuplicates(array){
      let unique = [];
      array.forEach(element => {
        if(!unique.includes(element)){
          unique.push(element)
        }
      });
      return unique;
    },
    validate(search_item){
      // check for null etc
      if(search_item !== 0 && search_item !== null && search_item !== undefined) {
        return true;
      }
      return false;
    },
    // handle writes to localstorage
    async callDataService(data_bpis){
      localStorage.setItem('image_bpis', data_bpis);
      data_bpis.forEach(async bpi_num => {
        if (bpi_num !== null) {
          await dataService.getAllImageDetails(bpi_num); 
        }
      })
    }
  }
